.SearchBar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 2.5rem;
}

.SearchBar input {
  width: 60%;
  padding: 0.5rem 0;
  color: #5680e9;
  border: 1px solid #faf9f6;
  border-radius: 8px;
  text-align: center;
  font-size: 1rem;
  margin-bottom: 1.5rem;
  background-color: #f4f6f7;
}

.SearchBar button {
  width: 15%;
  color: #f4f6f7;
  border: 1px solid #5680e9;
  border-radius: 8px;
  text-align: center;
  font-size: 1rem;
  background-color: #5680e9;
  padding: 0.5rem 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.SearchBar button:hover {
  background-color: #8860d0;
}

.searchButtonSpotifyLogo {
  height: 1.5rem;
  width: 1.5rem;
  justify-content: space-around;
}
